import React, { useState } from 'react';
import {
  Button,
  Input,
  PasswordInput,
} from '@ya.praktikum/react-developer-burger-ui-components';
import styles from '../basic-form-styles.module.scss';
import {Link, useHistory} from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { fetchUpdatePassword } from '../../services/slices/auth';

const ResetPasswordPage = () => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [form, setForm] = useState({
    password: '',
    token: '',
  });

  const onChange = e => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = () => {
    dispatch(fetchUpdatePassword(form));
    history.replace('/');
  };

  return (
    <div className={styles.container}>
      <form
        className={styles.form}
        onSubmit={handleSubmit}
      >
        <fieldset className={styles.wrapper}>
          <h1
            className={`text text_type_main-medium ${styles.heading}`}
          >
             
          </h1>
          <PasswordInput
            placeholder={'  '}
            value={form.password}
            name={'password'}
            onChange={onChange}
          />
          <Input
            value={form.token}
            name={'token'}
            onChange={onChange}
            size={'default'}
            placeholder={'   '}
          />
          <Button
            extraClass={styles.form__button}
            htmlType={'submit'}
            size={'medium'}
            onClick={handleSubmit}
          >
            
          </Button>
          <div className={styles.text__container}>
            <p
              className={`text text_type_main-default text_color_inactive`}
            >
               ?
            </p>
            <Link
              className={styles.link}
              to={'/login'}
            >
              
            </Link>
          </div>
        </fieldset>
      </form>
    </div>
  );
};

export default ResetPasswordPage;
